* {
    margin: 0;
    padding: 0;
}


html, body, .main, #root, .App {
    height: 100%;
  }

.fixHeight {
    height: 100%;
    width: 100%;
}


.side {
    height: 100%;
    width: 100%;
    margin: auto;
    /*background-color: black;*/
}

div {
    margin: auto;
}

.content {
    height: 100%;
    width: 100%;
}

.content-item {
    margin: 0 auto;
    height: 90%;
    width: 90%;
    background-color: #f7f7f7;
}

.grid {
    display: grid;
    grid-template-columns: minmax(200px, 200px) 1fr;
}

.item-1 {

}
.item-2 {
}

.nav-grid {
    display: grid;
    grid-template-rows: minmax(125px, 125px) repeat(2, 1fr) minmax(125px,125px); 
    text-align: center;
}

.nav-item-2 {
    letter-spacing: 0.5px;
}